/* ==========================================================================
   # Binder Document CTA
   ========================================================================== */

.c-binder-cta-wrapper {
	text-align: center;
	display: block;
	width: 100%;
}

.c-binder-cta {
	border: 1px solid #666;
	color: #666;
	text-decoration: none;
	margin-bottom: 1em;

	.c-binder-cta__meta {
		margin: 0px;
		padding: 0px;
		list-style: none;
		display: inline;

		.c-binder-cta__meta-item {
			margin: 0px;
			padding: 0px 0px 0px 10px;
			display: inline-block;
			list-style: none;

			.fa {
				width: 20px;
				padding-left: 0px;
			}
		}
	}

	.fa {
		padding-left: 10px;
	}

	&:hover {
		border: 1px solid #ccc;
		color: #ccc;
	}
}
